            <div>{{item.date}}</div>
            <button class="fun-btn" @click="onClick(item.link)">풀이보기</button>
          </div>

          <div v-else-if="item.site === 'SWexpert'" class="item swe" >
            <div class="item_title">{{ item.name }}
              <div>{{item.site}} {{item.number}}</div>
            </div>
            <div><strong class="item_disc">{{ item.disc }}</strong></div>
            <div>{{item.type}}</div>
            <div>{{item.date}}</div>
            <button class="fun-btn" @click="onClick(item.link)">풀이보기</button>
          </div>

          <div v-else-if="item.site === 'BOJ'" class="item boj" >
