          </div>

        </div>

      </div>
    </div>

    <div v-else-if="user_type =='BOJ'">
      <div class="" v-for="item in Algorithm" v-if="item.site =='BOJ'">

        <div :class="[ item.site === 'BOJ' ? 'item boj' : 'item swe']" v-if="check">
          <span id="simple_type">{{item.site}}</span>
          <span id="simple_name">{{item.name}}</span>
          <span class="simple_link" @click="onClick(item.link)"> 풀이보기</span>
        </div>

        <div v-else>
          <div class="item boj">
            <div class="item_title">{{ item.name }}
              <div>{{item.site}} {{item.number}}</div>
            </div>
            <div><strong class="item_disc">{{ item.disc }}</strong></div>
            <div>{{item.type}}</div>
            <div>{{item.date}}</div>
            <button class="fun-btn" @click="onClick(item.link)">풀이보기</button>
          </div>
        </div>
      </div>
    </div>

    <div v-else-if="user_type =='SWexpert'">
      <div class="" v-for="item in Algorithm" v-if="item.site =='SWexpert'">

        <div :class="[ item.site === 'BOJ' ? 'item boj' : 'item swe']" v-if="check">
          <span id="simple_type">{{item.site}}</span>
          <span id="simple_name">{{item.name}}</span>
          <span class="simple_link" @click="onClick(item.link)"> 풀이보기</span>
        </div>

        <div v-else>
          <div class="item swe">
            <div class="item_title">{{ item.name }}
              <div>{{item.site}} {{item.number}}</div>
            </div>

            <div><strong class="item_disc">{{ item.disc }}</strong></div>
            <div>{{item.type}}</div>
            <div>{{item.date}}</div>
            <button class="fun-btn" @click="onClick(item.link)">풀이보기</button>
          </div>
        </div>
      </div>
    </div>

  <div v-else-if="user_type =='Programmers'">
    <div class="" v-for="item in Algorithm" v-if="item.site =='Programmers'">

      <div :class="[ item.site === 'Programmers' ? 'item pro' : 'item swe']" v-if="check">
        <span id="simple_type">{{item.site}}</span>
        <span id="simple_name">{{item.name}}</span>
        <span class="simple_link" @click="onClick(item.link)"> 풀이보기</span>
      </div>

      <div v-else>
        <div class="item pro">
          <div class="item_title">{{ item.name }}
            <div>{{item.site}} {{item.number}}</div>
          </div>

